import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { tap } from 'rxjs/operators';
import { EventService } from 'src/app/shared/services/event.service';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private _user = null;
  get user(): any {
    return this._user;
  }
  set user(data: any) {
    this.eventService.user$.emit(data);
    this._user = data;
  }

  constructor(
    private http: HttpClient,
    private eventService: EventService
  ) {
    this.getUserData().subscribe((res) => {
      this.user = res.user;
    });
  }

  register(data: any): Observable<any> {
    const url = '/api/account/register';
    return this.http.post(url, data, {withCredentials: true}).pipe(
      tap(res => {
        this.user = res.user;
      })
    );
  }

  private getUserData(): Observable<any> {
    const url = '/api/account/me';
    return this.http.get(url);
  }

  login(data: any): Observable<any> {
    const url = '/api/account/login';
    return this.http.post(url, data, {withCredentials: true}).pipe(
      tap(res => {
        this.user = res.user;
      })
    );
  }

  logout(): Observable<any> {
    const url = '/api/account/logout';
    return this.http.post(url, {}, {withCredentials: true}).pipe(
      tap(res => {
        this.user = null;
      })
    );
  }

  verifyNumber(userOtp: string): Observable<any> {
    const url = '/api/account/verify-number';
    return this.http.post(url, {otp: userOtp}, {withCredentials: true}).pipe(
      tap(res => this.user = Object.assign(this.user, {is_verified: true}))
    );
  }

  sendOtpToNumber(): Observable<any> {
    const url = '/api/account/sendOtpToNumber';
    return this.http.post(url, {}, {withCredentials: true});
  }

  updateProfile(data: any): Observable<any> {
    const url = '/api/account/profile';
    return this.http.post(url, data).pipe(
      tap(res => this.user = res.user)
    );
  }
}
